body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tp-app {
  text-align: center;
}

.tp-app > form {
    width: 600px;
    height: 500px;
    margin: 30px auto 0 auto;
}

.tp-app > form label {
    display: block;
    text-align: left;
    font-weight: 500;
    width: 200px;
    margin: 0 0 10px 0;
}

.tp-app > form textarea {
    display: block;
    width: 100%;
    height: 400px;
}

.tp-error {
    color: red;
    font-weight: 500;
}

.tp-app > form button {
    float: left;
    margin-top: 10px;
    width: 150px;
    height: 30px;
}

#routeQuestionAnswers {
    text-align: left;
    width: 600px;
    margin: 30px auto 0 auto;
}
